<template>
	<main>
		<!-- 头部 -->
		<div class="tesco-top">
			<a href="#" @click="back()"></a>
			<p>购物车</p>
			<a href="#"><img src="../../assets/images/mored.png" alt=""></a>
		</div>
		<!-- 底部 -->
		<div class="tesco-bottom">
			<div>
				<router-link to="/like">
					<img src="../../assets/images/tesco01.png" alt="">
				</router-link>
				<router-link to="/classification">
					<img src="../../assets/images/tesco02.png" alt="">
				</router-link>
				<router-link to="/mytesco">
					<img src="../../assets/images/tesco03.png" alt="">
				</router-link>
				<router-link to="/search">
					<img src="../../assets/images/tesco04.png" alt="">
				</router-link>
				<router-link to="/buycart">
					<img src="../../assets/images/tesco05.png" alt="">
				</router-link>
			</div>
		</div>
		<!-- 购物车 -->
		<div class="buycart">
			<div class="header01">
				<div class="yuanquan1 " v-if="show02" @click="change()"></div>
				<div class="yuanquan1 yq" v-if="show01" @click="change()"></div>
				<div class="snzy">苏宁自营</div>
			</div>
			<div class="wbox">
				<div class="wboxleft">
					<a href="#">
						<img src="../../assets/images/kuaizi.jpg" alt="">
					</a>
					<div class="yuanquan1 " v-if="show02" @click="change()"></div>
					<div class="yuanquan1 yq" v-if="show01" @click="change()"></div>
				</div>
				<div class="wboxright">
					<p>  双枪 幸运三叶草合金筷 KZ4061 金色十双装</p>
					<p></p>
					<div class="null-cont"></div>
					<div class="wbox-bto">
						<div class="wbox-bto-left">
							<p>¥{{money}}  </p>
						</div>
						<div class="wbox-bto-right">
							<a href="#" @click="sub()"></a>
							<input type="text" :value="num">
							<a href="#" @click="add()"></a>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="jiesuan">
			<div>
				<div class="jiesuan01">
					<div class="yuanquan1 " v-if="show02" @click="change()"></div>
				<div class="yuanquan1 yq" v-if="show01" @click="change()"></div> 全选
				</div>
				<a href="#" :class="{jiesuan03:true,bs:show01==true}">去结算({{nums}})</a>
				<div class="jiesuan02">
					<p>合计：<span>￥{{numsum}}</span></p>
					<span>免运费</span>
				</div>
			</div>
		</div>
	</main>
</template>

<script>
</script>

<style>
	*{
		padding: 0px;
		margin: 0px;
	}
</style>
<script>
export default {
	data(){
		return{
			num:1,
			money:54,
			show01:false,
			show02:true,
			numsum:0,
			nums:0
		}
	},
	methods:{
		back(){
			this.$router.go(-1);
		},
		add(){
			this.num++;
			this.show01=true
			this.show02=false
			this.numsum=this.money*this.num
			this.nums=this.num
		},
		sub(){
			this.num--;
			if(this.num<=0){
				this.num=1;
			}
			this.show01=true
			this.show02=false
			this.numsum=this.money*this.num
			this.nums=this.num
		},
		change(){
			this.show01=!this.show01
			this.show02=!this.show02
			if(this.show01==true){
				this.numsum=this.money*this.num
				this.nums=this.num
				console.log(this.show01)
			}else{
				this.numsum=0
				this.nums=0
			}
		}
	},
	mounted(){
		// this.$http.get('./data/my.json')
		// .then((response)=> {
		// 	console.log(response);
		// 	this.listnav=response.data.listnav
		// })
		// .catch(function (error) {
		// 	console.log(error);
		// })
		// .then(function () {
		// 	// always executed
		// });
	}
}
</script>
<style scoped>
.jiesuan .bs{
	background-color: #f60;
}
    main{
		background-color: #f2f2f2;
		width: 100%;
	}
	.tesco-top{
		width: 100%;
		padding-bottom: 12%;
		background-color: #fff;
	}
	.tesco-top p{
		float: left;
		width: 76%;
		text-align: center;
		line-height: 40px;
	}
	.tesco-top a{
		display: block;
		float: left;
		width: 12%;
		padding-bottom: 12%;

	}
	.tesco-top a:nth-of-type(1){
		position: relative;
	}
	.tesco-top a:nth-of-type(1)::after{
		content:'';
		display: block;
		position: absolute;
		top: 35%;
		left: 40%;
		width: 30%;
		height: 30%;
		transform: rotate(-45deg);
		border: 1px solid #000;
		border-width: 1px 0px 0px 1px;
	}
	.tesco-top a:nth-of-type(2){
		padding-bottom: 0px;
	}
	.tesco-top a img{
		padding-top: 20%;
		width: 60%;
	}
	.tesco-bottom{
		position: fixed;
		bottom: 0px;
		width: 100%;
		background-color: #fff;
	}
	.tesco-bottom>div{
		display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: left;
		padding-top: 5px;
		padding-bottom: 10px;
	}
	.tesco-bottom a{
		display: block;
		width: 20%;
	}
	.tesco-bottom img{
		width: 45%;
		display: block;
		margin: 0 auto;
	}
	.cart{
		padding-top: 78px;
		text-align: center;
		padding-bottom: 30px;
	}
	.cart span{
		display:inline-block;
		width: 10%;
		padding-bottom: 18%;
		background: url('../../assets/images/null-cart.png') no-repeat;
		background-size: 100%;
	}
	.cart p{
		color: #666;
		padding-bottom: 10px;
	}
	.cart a{
		text-decoration: none;
		display: inline-block;background-color: #fff;
		padding: 5px 20px;
		border: 1px solid #666;
		color: #000;
	}
	.buycart{}
	.header01{
		background-color: #fff;
	}
	.yuanquan1{
		display: inline-block;
		width: 18px;
		height: 18px;
		border-radius: 50%;
		border: 1px solid #DCDCDC;
		margin: 10px 5px 0px 5px;
	}
	.yq{
		background: url('../../assets/images/yuanquan.png') center no-repeat #f60;
		background-size: 80%;
	}
	.snzy{
		max-width: 70%;
		word-break: keep-all;
		white-space: nowrap;
		text-overflow: ellipsis;
    	overflow: hidden;
		display: inline-block;
		color: #353d44;
		font-size: 14px;
		position: relative;
		padding-left: 30px;
	}
	.snzy::before{
		position: absolute;
		content:'';
		left: 5px;
		display: block;
		width: 18px;
		height: 18px;
		background: url('../../assets/images/Suning.png') no-repeat;
		background-size: 100%;
	}
	.wbox{
		display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: left;
		padding-top: 5%;
	}
	.wboxleft{
		width: 28%;
		display: inline-block;
		padding-left: 10%;
		padding-right: 2%;
		padding-bottom: 10%;
		position: relative;
	}
	.wboxleft img{
		width: 100%;
	}
	.wboxright{
		width: 60%;
		display: inline-block;
	}
	.wboxright p{
		font-size: 13px;
	}
	.wbox-bto-left p{
		color: #f60;
	}
	.null-cont{
		height: 55%;
	}
	.wbox-bto{
		display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: left;
	}
	.wbox-bto-left{
		width: 45%
	}
	.wboxleft .yuanquan1{
		position:absolute;
		top:30%;
		left: 2%;
	}
	.wbox-bto-right{
		width: 45%;
		display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: left;
	}
	.wbox-bto-right input{
		display: block;
		width: 50%;
		text-align: center;
	}
	.wbox-bto-right a{
		display: block;
		text-decoration: none;
		width: 21%;
		height: 20px;
		border: 1px solid #ccc;
	}
	.wbox-bto-right a:nth-of-type(1){
		background: url('../../assets/images/jian.png') no-repeat 5px 10px;
		background-size: 80%;
	}
	.wbox-bto-right a:nth-of-type(2){
		background: url('../../assets/images/add (1).png') no-repeat;
		background-size: 80%;
	}
	.jiesuan{
		position: fixed;
		bottom: 10%;
		width: 100%;
	}
	.jiesuan>div{
		position: relative;
	}
	.jiesuan02{
		float: right;
		position: relative;
		margin-right: 2%;
	}
	.jiesuan02 p{
		font-size: 12px;
		padding: 5px 0px;
	}
	.jiesuan02 p span{
		color: #f60;
		font-size: 16px;
		font-weight: bold;
	}
	.jiesuan02>span{
		font-size: 12px;
		position: absolute;
		right:0px;
	}
	.jiesuan01{
		display: inline-block;
		width: 20%;
	}
	.jiesuan03{
		float: right;
		width: 20%;
		background-color: #ccc;
		padding: 0px 10px;
		line-height: 50px;
		text-decoration: none;
		color: #fff;
	}
</style>


